.page {
  box-sizing: border-box;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  margin: 0;
  display: flex;
}

.page__clock {
  position: relative;
}

.watch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
}

.watch:before {
  content: "";
  z-index: 2;
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite clockwork;
  display: inline-block;
  position: absolute;
  bottom: 50%;
  left: 50%;
  transform: translateX(-50%)rotate(0);
}

.watch:after {
  content: "";
  z-index: 1;
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite clockwork;
  display: inline-block;
  position: absolute;
  bottom: 50%;
  left: 50%;
  transform: translateX(-50%)rotate(0);
}

.watch__center {
  z-index: 3;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: relative;
}

.watch--speed-up:before {
  animation-duration: 10s;
}

.watch--speed-up:after {
  animation-duration: 600s;
}

@keyframes clockwork {
  from {
    transform: translateX(-50%)rotate(0);
  }

  to {
    transform: translateX(-50%)rotate(360deg);
  }
}
